import React from "react";

const ClassificationText = ({}) => (
  <section className="text">
    <div>
      <h1>Classification</h1>
      <p>
        The first letter of each of these characteristics makes up the acronym
        <span id="text-1"> MRS GREN</span>. This is a good way of remembering
        them.
      </p>
      <p>
        Living organisms can
        <span id="text-2"> reproduce</span>
      </p>
      <p>
        Living organisms can also control their
        <span id="text-3"> internal conditions</span>, such as their temperature
        or water content.
      </p>
    </div>
  </section>
);

export default ClassificationText;



